<template>
  <div class="equipment-body">
    <div class="section-wrap">
      <div class="flex-wrapper">
        <div class="equipment-cell section-title">
          <h3>Computers</h3>
        </div>
        <div class="equipment-cell">
          <h3>gaming <icon name="gamepad" class="icon"></icon></h3>
          <div v-for="entry in equipmentList.gaming" :key="entry.$index">
            <h4>{{entry.type}}</h4>
            <ul v-for="item in entry.list" :key="item.$index">
              <li><a :title="item.title" :aria-label="'external link: ' + item.title + ' ' + entry.type" :href="item.link + equipmentList.affiliateTag" target="_blank">{{item.title}}</a></li>
              <li v-if="item.title2">{{item.title2}}</li>
            </ul>
          </div>
        </div>
        <div class="equipment-cell">
          <h3>streaming <icon name="film" class="icon"></icon></h3>
          <div v-for="entry in equipmentList.streaming" :key="entry.$index">
            <h4>{{entry.type}}</h4>
            <ul v-for="item in entry.list" :key="item.$index">
              <li><a :title="item.title" :aria-label="'external link: ' + item.title + ' ' + entry.type" :href="item.link + equipmentList.affiliateTag" target="_blank">{{item.title}}</a></li>
              <li v-if="item.title2">{{item.title2}}</li>
            </ul>
          </div>
        </div>
      </div>
      <div class="flex-wrapper">
        <div class="image-wrap">
          <figure v-parallax="-0.3">
            <img src="../assets/graphics/equipment-setup.jpg" alt="A picture of Lowco's setup from her instagram"/>
          </figure>
        </div>
      </div>
      <div class="flex-wrapper">
        <div class="equipment-cell section-title">
          <h3>Peripherals</h3>
        </div>
        <div class="equipment-cell peripherals">
          <div v-for="entry in equipmentList.peripherals" :key="entry.$index">
            <h4>{{entry.type}}</h4>
            <ul v-for="item in entry.list" :key="item.$index">
              <li><a :title="item.title" :aria-label="'external link: ' + item.title + ' ' + entry.type" :href="item.link + equipmentList.affiliateTag" target="_blank">{{item.title}}</a></li>
              <li v-if="item.title2">{{item.title2}}</li>
            </ul>
          </div>
        </div>
        <div class="equipment-cell peripherals">
          <div v-for="entry in equipmentList.peripherals2" :key="entry.$index">
            <h4>{{entry.type}}</h4>
            <ul v-for="item in entry.list" :key="item.$index">
              <li><a :title="item.title" :aria-label="'external link: ' + item.title + ' ' + entry.type" :href="item.link + equipmentList.affiliateTag" target="_blank">{{item.title}}</a></li>
              <li v-if="item.title2">{{item.title2}}</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  /* eslint-disable no-undef */
  name: 'Equipment',
  data () {
    return {
      equipmentList: _equipment !== undefined ? _equipment : null
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../variables.scss';
.image-wrap { 
  position: relative;
  overflow: hidden;
  height: 30vw;
  width: 100%;
  border-top: 3px solid $light-purple;
  border-bottom: 3px solid $light-purple;
  figure {
    position: absolute;
    width: 100%;
    top: -10vw;
    margin: 0;
    img {
      width: 100%;
    }
  }
}
.equipment-body {
  background-color: $dark-purple;
  text-align: center;
  padding-bottom: $standard-spacing;
  height: 100%;
  .section-wrap {
    padding: $standard-spacing5 0 0 0;
    margin: auto;
    .flex-wrapper {
      display: flex;
      max-width: 1200px;
      margin: 0 auto;
      justify-content: space-evenly;
      margin-bottom: $standard-spacing3;
      &:nth-child(2) {
        max-width: 100%;
      }
    }
  }
}
.equipment-cell {
  display: inline-block;
  color: $font-white;
  flex: 1 1 0;
  font-size: 1.06em;
  letter-spacing: 1.5px;
  min-width: 270px;
  h3 {
    color: $light-purple;
    font-size: 3em;
    margin: 0;
    margin-bottom: $standard-spacing;
    letter-spacing: 3px;
    text-transform: uppercase;
    text-align: right;
    padding-right: $standard-spacing2;
  }
  h4 {
    margin-bottom: $standard-spacing-half/2;
    margin-top: 1em;
    font-size: 1.25em;
    color: $light-purple;
    text-transform: uppercase;
  }
  ul {
    list-style: none;
    padding: 0 0 $standard-spacing-half 0;
    margin: 0;
  }
  li.title {
    font-weight: bold;
    font-size: 1.5em;
  }
  &:not(:first-child) {
    text-align: left;
    padding: 0;
    h3 {
      color: $font-gray;
      text-transform: lowercase;
      text-align: left;
    }
  }
  &:nth-child(2) {
    max-width: 300px;
    padding: 0 $standard-spacing2;
  }
  a {
    color: $font-white;
    &:hover,
    &:focus {
      color: $light-purple;
      text-decoration: underline;
    }
  }
  .icon {
    width: 35px;
    vertical-align: middle;
    fill: $font-white;
    height: auto;
  }
  &.peripherals {
    margin-top: -11px;
  }
}

@media (max-width: 910px) {
  .flex-wrapper {
    flex-wrap: wrap;
    padding-bottom: $standard-spacing-half;
  }
  .section-title {
    flex-basis: 100%;
  }
}

@media (max-width: 605px) {
  .flex-wrapper {
    flex-wrap: wrap;
    justify-content: center;
    .equipment-cell, .equipment-cell h3 {
      text-align: center;
      padding: 0;
    }
    figure {
      top: -45%;
    }
  }
  .section-title {
    flex-basis: 100%;
  }
}
</style>



